@import "normalizez.scss";
@import "resetz.scss";

* {
    box-sizing: border-box; }

@font-face {
    font-family: "Lexend";
    src: url('../fonts/Lexend-Regular.ttf');
    font-weight: 400;
    font-style: normal; }
@font-face {
    font-family: "Lexend";
    src: url('../fonts/Lexend-Medium.ttf');
    font-weight: 500;
    font-style: normal; }

@font-face {
    font-family: "Lexend";
    src: url('../fonts/Lexend-SemiBold.ttf');
    font-weight: 600;
    font-style: normal; }

@font-face {
    font-family: "Futura PT Medium";
    font-style: normal;
    font-weight: 500;
    src: url('../fonts/FuturaCyrillicMedium.ttf'); }


@keyframes scroll {
    from {
        transform: translateY(0); }
    to {
        transform: translateY(calc(-100% - 20px)); } }

@mixin scrollbar() {
    &::-webkit-scrollbar {
        width: 10px; }

    &::-webkit-scrollbar-track {
        margin-top: 96px;
        margin-bottom: 96px;
        margin-right: 20px;
        height: 10px;
        background-color: rgba(255,255,255, 0.12);
        border-radius: 10px; }

    &::-webkit-scrollbar-thumb {
        height: 10px;
        border: 4px solid rgba(0, 0, 0, 0);
        background-clip: content-box;
        border-radius: 7px;
        background-color: #478BF9; }

    &::-webkit-scrollbar-button {
        display: none; } }

@mixin anim($time, $delay) {
    -webkit-transition: all $time ease $delay;
    -moz-transition: all $time ease $delay;
    -ms-transition: all $time ease $delay;
    -o-transition: all $time ease $delay;
    transition: all $time ease $delay; }

@mixin scale($num) {
    -webkit-transition: scale($num);
    -moz-transition: scale($num);
    -ms-transition: scale($num);
    -o-transition: scale($num);
    transition: scale($num); }

@mixin rotate($deg) {
    -webkit-transition: rotate($deg);
    -moz-transition: rotate($deg);
    -ms-transition: rotate($deg);
    -o-transition: rotate($deg);
    transition: rotate($deg); }

html {
